import { mapState, mapActions } from 'vuex'
import upload from '@/components/upload'
import { uploadImg, delUploadImg } from '@/common/js/common'
import { feedback } from '@/api'
export default {
	name: '',
	data() {
		return {
			type: '',
			desc: '',
			phone: '',
			imgList: [],
			baseList: [],
			itemList: ['物流太慢', '服务态度差', '质量一般', '价格略贵']
		}
	},
	onLoad() {
		this._reqGetFeedbackList()
	},
	onShow () {
		this.$hideTabbarButton()
	},
	computed: {
		...mapState(['feedback_list'])
	},
	methods: {
		...mapActions(['reqGetFeedbackList']),
		async onSubmitClick () {
			if(this.type == '') {
				uni.showToast({
					title: '请选择反馈类型',
					icon: 'none'
				})
				return;
			}
			if(this.desc == '') {
				uni.showToast({
					title: '请输入您的反馈内容',
					icon: 'none'
				})
				return;
			}
			let result = await feedback ({
				token: uni.getStorageSync('token'),
				images: this.baseList,
				type: this.type,
				text: this.desc,
				qq: this.phone
			})
			if(result.code == 2000) {
				uni.showToast({
					title: '提交成功'
				})
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			}
		},
		_reqGetFeedbackList() {
			this.reqGetFeedbackList({
				token: uni.getStorageSync('token')
			})
		},
		onAddImgEvent () {
			let self = this;
			uploadImg(self.baseList, self.imgList)
		},
		onDelImgEvent (index) {
			let self = this;
			delUploadImg(self.baseList, self.imgList, index)
		},
		onChoseFeedbackType() {
			let self = this;
			uni.showActionSheet({
				itemList: this.itemList,
				success: function(res) {
					self.type = self.itemList[res.tapIndex]
				},
			});
		}
	},
	components: {
		upload
	}
}
